import { configureStore } from '@reduxjs/toolkit'
import game from '../features/game/gameSlice'

let persistedState = (
  localStorage && localStorage.getItem('ZombieChamber-GameSave') ?
  JSON.parse(localStorage.getItem('ZombieChamber-GameSave')) : {}
)

console.log(
  persistedState.game.stats.bulletsFired
)

const store = configureStore({
  reducer: {
    game
  },
  preloadedState: {
    persistedState
  }
})

store.subscribe(() => {
  if (localStorage) {
    localStorage.setItem('ZombieChamber-GameSave', JSON.stringify(store.getState()))
  }
})


export default store
